/* main modules
 * include title, text, label, input and components depending on the page
 * setting loader
 */

.layout-main
    position: relative
    z-index: var(--z-index-loader)
    display: flex
    flex-direction: column
    align-items: center
    overflow-x: hidden
    background-image: url(../../image/main-image.gif)
    background-repeat: no-repeat
    background-size: cover
    background-blend-mode: soft-light
    background-color: rgb(0 0 0 / 70%)

/* setting main title and main text for pages: Home, error, GIT, download */

.main-title
    margin-bottom: auto
    padding-top: 2rem
    text-align: center
    font-size: var(--adaptive-size-header)
    font-family: var(--font-header-bold)
    text-shadow: 0 0 5px var(--purple)
    user-select: none
    transition: opacity 1.25s
    color: rgba(0 0 0 / 100%)

.main-text
    margin-bottom: auto
    font-size: calc(1.2rem + 0.5vw)
    font-family: var(--font-text)
    text-align: center
    line-height: 1.5
    text-shadow: 0 0 5px var(--aqua), 0 -2.5px 7px var(--white)
    user-select: none
    transition: opacity 1.25s
    color: rgba(0 0 0 / 100%)

.main-button
    color: var(--purple)
    height: 5rem
    margin-top: 1rem
    margin-bottom: calc(18vh + 5vw)
    padding: 1rem
    outline: none
    border: 0.06rem solid
    border-radius: 0.5rem
    font-size: 1rem
    font-family: var(--font-header-bold)
    text-transform: uppercase
    background-color: transparent
    box-shadow: 0 0 12px 2px
    cursor: pointer
    animation: change-button-color 40s linear infinite
    transition: box-shadow 280ms, opacity 2s
    &:hover
        box-shadow: 0 0 30px 8px
    &:focus
        box-shadow: 0 0 30px 8px

@keyframes change-button-color
    10%
        color: var(--purple)
    20%
        color: var(--aqua)
    30%
        color: var(--cyan)
    40%
        color: var(--green)
    50%
        color: var(--lime)
    60%
        color: var(--lime)
    70%
        color: var(--green)
    80%
        color: var(--cyan)
    90%
        color: var(--aqua)
    100%
        color: var(--purple)

/* setting label, input and components for pages: Components */

.main-label
    padding-top: 2.5vw
    padding-left: 1.5vw
    padding-right: 1.5vw
    user-select: none
    text-align: center
    font-family: var(--font-navigation)
    color: var(--silver)
    cursor: pointer
    transition: opacity 1.25s

.main-input
    background-color: transparent
    border: none
    outline: none
    margin-top: 0.5rem
    margin-left: 0.5rem
    padding-top: 0.35rem
    padding-left: 0.25rem
    padding-bottom: 0.35rem
    border-radius: 0.25rem
    font-size: 1.125rem
    font-weight: bold
    font-family: var(--font-navigation)
    box-shadow: 0 0 8px 2px var(--purple)
    color: var(--silver)
    transition: box-shadow 1.125s
    &:focus
        box-shadow: 0 0 8px 2px var(--aqua)

.components
    display: grid
    justify-items: center
    row-gap: 1.5rem
    margin-top: 5vw
    margin-bottom: auto
    user-select: none
    transition: transform 1s, opacity 1.25s

.components-list
    text-shadow: 0 0 5px var(--whitesmoke), 0 -2.5px 7px var(--gray)

.components-rar
    color: black
    text-shadow: 0 0 5px var(--aqua)
    text-transform: uppercase
    font-weight: bold
    font-family: var(--font-header)

/* setting loader */

.loader
    width: 12rem
    height: 12rem
    display: flex
    justify-content: center
    align-items: center
    margin-top: auto
    margin-bottom: auto
    border-radius: 50%
    box-shadow: 0 0 10px 2px var(--brown)
    transition: opacity 2.125s

.loader-time
    width: 3rem
    height: 3rem
    border-width: 0.2rem
    border-style: solid
    border-color: var(--purple)
    border-radius: 50%
    text-align: center
    line-height: 3rem
    font-weight: bold
    font-family: var(--font-navigation)
    color: var(--purple)
    box-shadow: 0 0 8px 1px var(--purple)
