/*
    Accordion
    sass version
 */

.accordion
    max-width: 800px
    display: flex
    justify-content: center
    color: var(--purple, rgb(170, 0, 255))
    &__box
        flex: 0 1 10%
        padding: 0 0.5rem
        transition: flex-basis 2s
        &:hover
            flex-basis: 33.3%

.box
    position: relative
    overflow: hidden
    border-radius: 0.5rem
    cursor: pointer
    &__heading
        transform: rotate(90deg) translateX(70px)
        font-family: var(--font-handwritten-bold, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        white-space: nowrap
        transition: transform 2s
    &__content
        width: 0
        height: 0
        opacity: 0
        font-family: var(--font-navigation, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        font-weight: bold
        padding-bottom: 3rem
        transition: width 400ms, opacity 200ms, height 0s 2s
    &__footer
        display: flex
        justify-content: center
        padding-bottom: 3rem
    &__tag
        width: 75px
        border: 1px solid
        padding: 0.25rem
        text-align: center
        font-family: var(--font-handwritten, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        border-radius: 0.25rem
        transform: rotate(90deg) translateY(400px)
        transition: transform 1s
        &--first
            transform: rotate(90deg) translateY(400px)
        &--last
            transform: rotate(90deg) translateY(400px)
    &:hover &__tag
        transform: rotate(90deg)
        transition-delay: 2s
    &:hover &__tag
        &--first
            transform: rotate(90deg) translateY(-45px)
            transition-delay: 3s
    &:hover &__tag
        &--last
            transform: rotate(90deg) translateY(45px)
            transition-delay: 1s
    &:hover &__content
        width: 100%
        height: 50px
        opacity: 1
        transition: opacity 2s 2s
    &:hover &__heading
        transform: rotate(0) translateX(50%)
    &::after
        content: " "
        position: absolute
        z-index: -1
        top: 0
        right: 0
        bottom: 0
        left: 0
        opacity: 0.1
        transition: background-color 800ms
    &:hover:after
        background-color: currentColor

@media screen and (max-width: 36.25rem)
    .accordion
        flex-direction: column
        justify-content: flex-start
        &__box
            height: 8%
            &:hover
                flex-basis: 40%

    .box
        &__heading
            transform: none
        &__content
            width: 100%


