/* header modules
 * navigation
 * text logo
 * burger menu
 * header loading
 */

.layout-header
    display: flex
    justify-content: space-between
    align-items: center
    border-bottom-width: 0.1rem
    border-bottom-style: solid
    border-bottom-color: var(--aqua)

/* setting navigation ( main menu) */

.navigation
    flex: 1
    display: flex
    flex-wrap: wrap
    align-items: center
    margin-top: 0.5rem
    margin-bottom: 0.5rem
    font-size: 1rem
    transition: transform 1.125s

.navigation-list-item
    border-width: 0.1rem
    border-style: solid
    border-color: var(--purple)
    outline: none
    margin-top: 0.25rem
    margin-right: 0.5rem
    margin-left: 0.5rem
    margin-bottom: 0.25rem
    padding-top: 0.25rem
    padding-right: 1rem
    padding-left: 1rem
    padding-bottom: 0.25rem
    transform: skewX(-10deg)
    line-height: 1.4
    border-radius: 0.30rem
    text-transform: uppercase
    font-family: var(--font-navigation)
    user-select: none
    cursor: pointer
    color: var(--aqua)
    transition: transform 185ms cubic-bezier(1,.69,.94,.77), border-color 185ms, box-shadow 185ms cubic-bezier(0,.02,.92,.13), color 180ms, background-color 185ms
    &:focus
        border-color: transparent
        box-shadow: 0 0 5px 2px var(--aqua)
    &:hover
        transform: scale(1.1) skewX(-10deg)
        color: whitesmoke
        border-color: transparent
        box-shadow: 0 0 7px 2px rgb(240 248 255)
    &:active
        transform: scale(1) skewX(-10deg)
        box-shadow: none
        border-color: var(--purple)
        background-color: transparent

@media screen and ( max-width: 38.75em )
    .navigation-list-item
        padding-top: 0.45rem
        padding-bottom: 0.45rem
        transform: none
        &:hover
            transform: none
        &:active
            transform: none

[data-active]
    border: none
    box-shadow: 0 0 5px 2px var(--aqua)

/* setting burger-menu */

.burger-menu
    position: relative
    z-index: var(--z-index-navigation)
    display: none
    flex-direction: column

@media screen and ( max-width: 38.75em )
    .burger-menu
        display: flex

.burger
    width: 50px
    height: 50px
    margin-left: 0.5rem
    display: flex
    flex-direction: column
    justify-content: space-evenly

.burger-line
    height: 0.4rem
    border: 0.06rem solid var(--purple)
    background-color: transparent
    box-shadow: 0 0 7px 1px #121fbc
    transition: transform 1.25s,background-color 1s, height 500ms, border-color 350ms

.burger-navigation
    position: absolute
    z-index: var(--z-index-navigation)
    top: 3.2rem
    left: 0
    width: 0
    height: 100vh
    overflow: hidden
    background-image: linear-gradient(45deg, black 75%, transparent)
    transition: width 750ms cubic-bezier(1,.01,.66,.8)

.burger-navigation .navigation
    width: 100vw

.burger-navigation .navigation-list-item
    width: inherit

/* setting text-logo */

.text-logo
    margin-left: auto
    font-size: 2.2rem
    color: black
    font-family: var(--font-header-bold)
    user-select: none
    transition: opacity 750ms

.first-logo-color
    text-shadow: 0 0 2.5px var(--silver)

.last-logo-color
    color: var(--purple)
    text-shadow: 0 0 5px var(--purple)

/* setting header-loading */

.header-loading
    position: absolute
    z-index: var(--z-index-loader)
    top: 0.65rem
    width: 100%
    text-align: center
    font-size: 2rem
    user-select: none
    font-family: var(--font-handwritten-bold)
    color: wheat
    text-shadow: 0 0 10px var(--purple)
    transition: opacity 1.125s

@media screen and ( max-width: 36.25em )
    .header-loading
        top: 0.50rem
        right: 0.5rem
        text-align: initial
        width: initial
