/* state modules
 * includes all states for this app
 */

/* setting overflow */

.is-overflow-y-hidden
    overflow-y: hidden

/* setting colors */

.is-silver
    background-color: rgb(204, 204, 204)

.is-dark
    background-color: rgb(68, 68, 68)

.is-cyan
    background-color: rgb(77, 144, 254)

.is-blue
    background-color: rgb(17, 17, 204)

.is-pink
    background-color: rgb(233, 30, 99)

.is-red
    background-color: rgb(213, 0, 0)

.is-purple
    background-color: rgb(170, 0, 255)

.is-lime
    background-color: rgb(0, 96, 100)

.is-brown
    background-color: rgb(93, 64, 55)

.is-orange
    background-color: rgb(221, 75, 57)

.is-yellow
    background-color: rgb(221, 142, 39)

.is-color-silver
    color: rgb(204, 204, 204)

.is-color-dark
    color: rgb(68, 68, 68)

.is-color-cyan
    color: rgb(77, 144, 254)

.is-color-blue
    color: rgb(17, 17, 204)

.is-color-pink
    color: rgb(233, 30, 99)

.is-color-red
    color: rgb(213, 0, 0)

.is-color-purple
    color: rgb(170, 0, 255)

.is-color-lime
    color: rgb(0, 96, 100)

.is-color-brown
    color: rgb(93, 64, 55)

.is-color-orange
    color: rgb(221, 75, 57)

.is-color-yellow
    color: rgb(221, 142, 39)

/* setting width */

.is-width-100vw
    width: 100vw

/* setting shows */

.is-hide
    opacity: 0

/* setting transforms */

.is-left-translateX
    transform: translateX(-100%)

.is-right-translateX
    transform: translateX(100%)

/* setting states for other components */

.is-line-none
    border-color: transparent
    background-color: transparent
    box-shadow: none

.is-line-top
    transform: rotate(40deg) translate(10px, 12px)
    height: 3px
    background-color: var(--purple)

.is-line-bottom
    transform: rotate(-40deg) translate(8px,-12px)
    height: 3px
    background-color: var(--purple)

.dom-loaded
    transition: transform 1.5s

/* disabled for input (components section) */

.is-disabled
    color: var(--silver)
    box-shadow: 0 0 8px 2px var(--silver)
    &:focus
        box-shadow: 0 0 8px 2px var(--silver)

/* transformations for components */

.is-transform-1
    transform: translateX(100vw)

.is-transform-2
    transform: translateX(-100vw)

.is-transform-3
    transform: rotate(45deg) translateX(150vw)

.is-transform-4
    transform: rotate(45deg) translateX(-150vw)
