/* module for error
 * include style for error block
 */

.error
    position: absolute
    z-index: var(--z-index-global)
    top: 1rem
    right: 1rem
    width: 35vw
    border: 0.06rem solid var(--red)
    padding: 0.5rem
    border-radius: 0.5rem
    font-size: 1rem
    background-color: black
    user-select: none
    transition: opacity 2s

.error-title
    text-align: center
    font-family: var(--font-header-bold)
    text-shadow: 0 0 5px var(--red)
    color: black

.error-content
    color: var(--pink)
    font-family: var(--font-navigation)
