/*
    Menu
    sass version
 */

.menu
    display: inline-block
    margin: 0
    padding: 0
    transform: perspective(800px) rotateX(35deg)
    box-shadow: 0 0 15px
    color: var(--purple, rgb(170, 0, 255))
    transition: transform 800ms 350ms, color 2s
    &:hover
        transition: transform 800ms
        transform: perspective(800px) rotateX(0)
    &__list,
    &__under-list
        border: 0.06rem solid
        border-bottom: none
        padding: 0.85rem 2rem
        text-align: center
        list-style: none
        background-color: black
        cursor: pointer
        transition: transform 300ms, box-shadow 280ms
        &:first-child
            border-radius: 0.25rem 0.25rem 0 0
        &:last-child
            border-bottom: 0.06rem solid
            border-radius: 0 0 0.25rem 0.25rem
    &__list
        position: relative
        display: grid
        grid-auto-flow: column
        align-items: center
        column-gap: 0.25rem
        &:hover
            box-shadow: inset 0 -5px 15px 0
        &:hover > .menu__under--html
            transition: transform 200ms 150ms
            transform: perspective(600px) rotateY(30deg) translateX(-165px)
        &:hover > .menu__under--css
            transition: transform 800ms 150ms
            transform: rotate3d(1, 1, 1, 0deg) translate(170px, 0) scale(1)
    &__under-list
        transition: background-color 200ms
        &:hover
            background-color: currentColor
    &__text
        font-family: var(--font-navigation, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        color: var(--whitesmoke, rgb(254, 249, 219))
    &__image
        width: 25px
        height: 25px
    &__under
        position: absolute
        z-index: -1
        margin: 0
        padding: 0
    &__under--html
        top: 0
        left: 0
        transform: perspective(600px) rotateY(30deg)
        transition: transform 200ms 350ms
    &__under--css
        top: 0
        right: 0
        transition: transform 800ms 350ms
        transform: rotate3d(2, 4, 1, 75deg) translate(400px, -400px) scale(0)

@media screen and (max-width: 35.93em)
    .menu
        position: relative
        display: grid
        grid-auto-flow: column
        grid-gap: 0.5rem
        transform: none
        box-shadow: none
        &__list,
        &__under-list
            position: static
            padding: 5vw
            border-radius: 0.25rem
            border-bottom: 0.06rem solid
            box-shadow: inset 0 0 15px 0
            &:hover
                box-shadow: inset 0 0 15px 0
            &:last-child,
            &:first-child
                border-radius: 0.25rem
        &__under-list
            border-radius: 0
            &:last-child,
            &:first-child
                border-radius: 0
        &__text
            width: 0
            overflow: hidden
        &__under
            width: 100%
        &__list
            &:hover > .menu__under--html
                transform: translateX(0)
                opacity: 1
                transition: transform 200ms
            &:hover > .menu__under--css
                transform: translateX(0)
                opacity: 1
                transition: transform 200ms
        &__under--html
            top: 100px
            transform: translateX(-100vw)
            opacity: 0
            transition: transform 200ms
        &__under--css
            top: 100px
            transform: translateX(100vw)
            opacity: 0
            transition: transform 200ms


