/* basis style
 * setting reset
 * setting fonts
 * creating animate
 */

body
    margin: 0
    font-family: system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"
    background-color: #272727
    animation-name: body-color
    animation-duration: 15s
    animation-iteration-count: infinite

div,
header,
main,
section,
article,
aside,
footer
    display: block

h1,
h2,
h3,
h4,
h5,
h6
    margin: 0
    padding-top: 0.25rem
    padding-right: 0.5rem
    padding-left: 0.5rem
    padding-bottom: 0.25rem

p
    margin: 0
    padding-top: 0.125rem
    padding-right: 0.2rem
    padding-left: 0.2rem
    padding-bottom: 0.125rem

ul
    margin: 0
    padding: 0

li,
ol
    list-style: none

a
    text-decoration: none
    color: var(--purple)
    text-shadow: none

@font-face
    font-family: Freckle
    src: url(../../fonts/freckle/FreckleFace-Regular.woff) format("woff")

@font-face
    font-family: Jura
    src: url(../../fonts/jura/Jura-VariableFont_wght.woff) format("woff")

@font-face
    font-family: Luckiest
    src: url(../../fonts/luckiest/LuckiestGuy-Regular.woff) format("woff")

@font-face
    font-family: Passion
    src: url(../../fonts/passion/PassionOne-Regular.woff) format("woff")

@font-face
    font-family: Satisfy
    src: url(../../fonts/satisfy/Satisfy-Regular.woff) format("woff")

@font-face
    font-family: Vollkorn
    src: url(../../fonts/vollkorn/Vollkorn-Regular.woff) format("woff")

@font-face
    font-family: VollkornItalic
    src: url(../../fonts/vollkorn/Vollkorn-MediumItalic.woff) format("woff")

@keyframes body-color
    0%
        background: rgb(26, 21, 39)
    25%
        background: rgb(29, 21, 39)
    50%
        background: rgb(33, 21, 39)
    75%
        background: rgb(29, 21, 39)
    100%
        background: rgb(26, 21, 39)
