/* setting root directory
 * setting font variables
 * setting font size variables
 * setting colors variables
 * setting zet index variables
 */

\:root
    --font-header: Passion
    --font-header-bold: Luckiest
    --font-handwritten-bold: Freckle
    --font-handwritten: Satisfy
    --font-navigation: Jura
    --font-text: Vollkorn
    --font-text-italic: VollkornItalic

    --adaptive-size-header: calc(1.9rem - 0.125vw)
    --adaptive-size-text: calc(1.5rem - 0.4vw)

    --white: rgb(235, 235, 235)
    --whitesmoke: rgb(254, 249, 219)
    --silver: rgb(204, 204, 204)
    --gray: rgb(118, 118, 118)
    --dark: rgb(68, 68, 68)
    --aqua: rgb(24, 206, 255)
    --cyan: rgb(77, 144, 254)
    --blue: rgb(17, 17, 204)
    --pink: rgb(233, 30, 99)
    --red: rgb(213, 0, 0)
    --purple: rgb(170, 0, 255)
    --green: rgb(76, 175, 80)
    --lime: rgb(0, 96, 100)
    --brown: rgb(93, 64, 55)
    --yellow: rgb(221, 142, 39)
    --orange: rgb(221, 75, 57)

    --z-index-null: -10
    --z-index-loader: 10
    --z-index-navigation: 20
    --z-index-modal: 30
    --z-index-global: 10000