/*
    Form
    sass version
 */

.form
    display: inline-block
    color: var(--purple, rgb(170, 0, 255))
    transition: color 2s
    &__group
        display: grid
        grid-auto-flow: column
        grid-gap: 1.5rem
        margin-bottom: 1.25rem
        transition: opacity 2s, visibility 2s
        &--checkbox
            width: min-content
            grid-gap: 0.55rem
            align-items: end
            margin-top: 2rem
        &--hidden
            opacity: 0
            visibility: hidden
    &__label
        display: flex
        flex-direction: column
        &--row
            flex-direction: row
    &__input
        outline: none
        border: none
        padding: 0.25rem 3.5rem 0.25rem 1rem
        font-size: 1rem
        font-family: var(--font-navigation, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        font-weight: bolder
        background-color: transparent
        color: inherit
        box-shadow: inset 0 0 3px
        transition: box-shadow 180ms
        &:focus
            box-shadow: inset 0 0 8px
    &__label-lang
        display: block
    &__checkbox
        width: 25px
        height: 25px
        display: inline-block
        border: 0.06rem solid
        border-radius: 0.25rem
        cursor: pointer
        transition: border-color 180ms, box-shadow 180ms
        &--ml
            margin-left: 0.25rem

.label
    font-family: var(--font-navigation, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
    font-weight: bold
    cursor: pointer
    &--middle
        vertical-align: super
    &__text
        padding-right: 0.5rem
        padding-bottom: 0.25rem
    &__background
        position: relative
        max-width: 250px
        clip-path: polygon(0 0, 44% 0, 100% 0, 100% 0, 77% 100%, 44% 100%, 0 100%, 4% 52%)
        &::after
            content: " "
            position: absolute
            z-index: -1
            top: 0
            left: 0
            right: 0
            bottom: 0
            opacity: 0.15
            background-color: currentColor
            transition: background-color 200ms

.checkbox
    appearance: none
    position: absolute
    opacity: 0
    &:active + .form__checkbox
        border-color: transparent
        box-shadow: 0 0 10px 2px
    &:focus + .form__checkbox
        border-color: transparent
        box-shadow: 0 0 10px 2px
    &:checked + .form__checkbox
        border-color: transparent
    &:checked + .form__checkbox > .checkbox-svg
        opacity: 1
    &:checked ~ .form__group--hidden
        visibility: visible
        opacity: 1

.checkbox-svg
    opacity: 0
    transition: opacity 180ms

::-webkit-input-placeholder
    color: currentColor

@media screen and (max-width: 37.5em)
    .form
        &__group
            &--row
                grid-auto-flow: row


