/*
    Card
    sass version
 */

.card
    position: relative
    max-width: 380px
    padding: 0 0.5rem
    font-size: 1rem
    color: var(--purple, rgb(170, 0, 255))
    transition: color 2s
    &:hover > .card__body > .card__image
        transform: rotate(-10deg) scale(1.1)
    &:hover > .card__body > .card__info
        transform: translateX(0)
    &:hover > .card__body > .card__content
        opacity: 1
    &:hover > .card__footer > .card__tags
        transform: translateX(0)
    &__header
        display: flex
        justify-content: space-between
        align-items: center
    &__title
        margin: 0
        padding: 0
        font-size: 1.4rem
        font-family: var(--font-header, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        letter-spacing: 0.5px
    &__price
        font-family: var(--font-navigation, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
    &__cost
        display: inline-block
        transition: transform 180ms
        &:hover
            transform: translateX(-5px) scale(1.4)
    &__currency
        display: inline-block
        animation: currency 1s linear infinite
    &__body
        overflow: hidden
        display: grid
        row-gap: 0.5rem
    &__button
        outline: none
        border: 0.06rem solid
        padding: 0.5rem 1rem
        border-radius: 0.25rem
        background-color: transparent
        font-family: var(--font-navigation, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        text-transform: uppercase
        color: inherit
        user-select: none
        cursor: pointer
        transition: transform 180ms, border-color 180ms, color 180ms, background-color 180ms, box-shadow 180ms
        &:focus
            border-color: transparent
            box-shadow: 0 0 12px 2px
        &:hover
            border-color: transparent
            background-color: rgba(32, 23, 51, 0.7)
            box-shadow: 0 0 12px 2px
        &:active
            border-color: currentColor
            box-shadow: none
        &--image
            position: absolute
            top: -100px
            background-color: rgba(32, 23, 51, 0.7)
            transition: top 500ms
        &--left
            margin-left: 0.75rem
        &--right
            margin-right: 0.75rem
    &__image-container
        position: relative
        display: flex
        flex-direction: column
        align-items: center
        &:hover > .card__image
            filter: blur(5px) opacity(0.75)
        &:hover > .card__button--image
            top: 3rem
    &__image
        width: 100%
        height: 150px
        transition: filter 500ms
    &__button-container
        width: 100%
        display: flex
        justify-content: space-between
    &__content
        opacity: 0
        margin: 0
        padding: 0 0.75rem
        text-align: justify
        font-family: var(--font-text, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        transition: opacity 500ms 1500ms
    &__info
        width: 100%
        display: flex
        justify-content: space-evenly
        transform: translateX(-400px)
        transition: transform 500ms 1000ms
    &__quantity,
    &__view
        font-family: var(--font-navigation, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
    &__footer
        overflow: hidden
        display: flex
        flex-wrap: wrap
        justify-content: space-between
    &__tags
        flex: 1
        display: flex
        justify-content: space-evenly
        font-family: var(--font-handwritten, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        transform: translateX(400px)
        transition: transform 500ms 500ms
    &__tag
        display: inline-block
        border: 0.06rem solid
        margin: 1rem 0
        padding: 0.15rem 1.5rem
        border-radius: 0.25rem

@keyframes currency
    to
        transform: rotateY(360deg)

