/*
    Table
    sass version
 */

.table
    /* overflow-x: hidden */ /* uncomment this */
    width: 100%
    max-width: 500px
    display: block
    border-collapse: collapse
    color: var(--purple, rgb(170, 0, 255))
    transition: color 2s
    &__caption
        font-family: var(--font-header-bold, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        font-size: 1.2rem
        caption-side: bottom
        text-align: left
    &__thead
        font-family: var(--font-header, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        font-size: 1.2rem
        letter-spacing: 0.5px
    &__tbody
        font-family: var(--font-navigation, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
    &__tbody &__tr:nth-child(odd)
        position: relative
        &:after
            content: " "
            position: absolute
            z-index: -1
            top: 0
            bottom: -1px
            left: 0
            right: 0
            background-color: currentColor
            opacity: 0.25
    &__tbody &__tr
        transition: box-shadow 180ms
        &:hover
            box-shadow: inset 0 0 8px 2px
        &:nth-child(odd)
            transform: translateX(-200vw)
            animation: table-translate 1s 2s forwards
        &:nth-child(even)
            transform: translateX(200vw)
            animation: table-translate 1s 2s forwards
    &__tr
        border-bottom: 0.06rem solid
    &__td
        padding: 0.5rem 1.5rem
        &--hidden
            filter: blur(2.5px)
    &__image
        width: 25px
        height: 25px
        &--color
            fill: currentColor

@keyframes table-translate
    to
        transform: translateX(0)

/*
 * Setting table adoptable
 */

@media screen and (max-width: 26.875em)
    .table
        overflow-x: auto


