/* grid module
 * setting main container
 * setting header
 * setting main
 * setting footer
 */

.grid-container
    display: grid
    grid-template-columns: [start] 1fr [middle] 1fr [middle-end] 1fr [start-end]
    grid-template-rows: [start] auto [middle] 1fr [bottom] auto [start-end]
    height: 100vh
    overflow: hidden

.layout-header
    grid-column: start / end
    grid-row: start / middle

.layout-main
    grid-column: start / end
    grid-row: middle / bottom

.layout-footer
    grid-column: start / end
    grid-row: bottom / start-end
