/*
    Slider
    sass version
 */

.slider
    position: relative
    z-index: var(--z-index-modal, 30)
    width: 70vw
    perspective: 1000px
    border-radius: 0.25rem
    color: var(--purple, rgb(170, 0, 255))
    transition: color 2s
    &__slide
        position: absolute
        z-index: -1
        top: 0
        border: 0
        right: 0
        left: 0
        width: 100%
        min-height: 100px
        box-shadow: 0 0 10px
        &::after
            content: " "
            position: absolute
            top: 0
            bottom: 0
            left: 0
            right: 0
            background-color: currentColor
            opacity: 0.1
        &--1
            animation: slide 18s linear infinite
            animation-delay: 6s
        &--2
            transform: translateX(100vw)
            opacity: 0
            animation: slide-show 2s linear forwards, slide 18s linear infinite
            animation-delay: 6.5s, 12s
        &--3
            transform: translateX(100vw)
            opacity: 0
            animation: slide-show 2s linear forwards, slide 18s linear infinite
            animation-delay: 12.5s, 18s
    &__heading
        text-align: right
        padding: 0.5rem
        font-family: var(--font-header, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
        &--bold
            padding: 0
            text-align: center
            line-height: 100px
            font-family: var(--font-header-bold, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")
    &__content
        padding: 0.5rem
        font-family: var(--font-handwritten-bold, system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji")

@keyframes slide-show
    to
        opacity: 1
        transform: translate(0)

@keyframes slide
    0%
        transform: none
    10%
        transform: rotateY(90deg)
    15%
        opacity: 0
        transform: translateX(-100vw) rotateY(90deg)
    20%, 30%
        transform: scale(0)
    40%, 50%, 60%, 70%
        opacity: 0
        transform: translateX(100vw)
    80%, 90%, 100%
        opacity: 1
        transform: translateX(0)


