.colors
    display: grid
    grid-template-rows: auto auto
    grid-auto-flow: column
    justify-items: center
    gap: 1rem
    order: -1

.color
    width: 2rem
    height: 2rem
    border-radius: 0.25rem
    cursor: pointer
    transition: transform 180ms
    &:hover
        transform: scale(1.1)
    &:active
        transform: scale(1)
